import { PartnerDetailsComponent } from './partner-details/partner-details.component';
import { CrossBorderHomeComponent } from './cross-border-home/cross-border-home.component';

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const appRoutes: Routes = [
  {path: 'details', component: PartnerDetailsComponent},
  {path: '', component: CrossBorderHomeComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class CrossBorderRoutingModule {
}
