import { PartnerDetailsComponent } from './../partner-details/partner-details.component';
import { FormControl, Validators, NgForm, FormGroupDirective } from '@angular/forms';
import { CrossBorderUtilService } from './../services/cross-border-util.service';
import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, ErrorStateMatcher } from '@angular/material';


@Component({
  selector: 'app-create-new-partner',
  templateUrl: './create-new-partner.component.html',
  styleUrls: ['./create-new-partner.component.css']
})
export class CreateNewPartnerComponent implements OnInit {

  @ViewChild('partnerInfo') partnerInfomation: NgForm;
  successData = { prop1: '1st prop', prop2: '2nd prop' };
  referenceId;
  participantName = '';
  saveSuccess: Boolean = false;
  constructor(private crossBorderUtilService: CrossBorderUtilService,
    public dialogRef: MatDialogRef<CreateNewPartnerComponent>,
    @Inject(MAT_DIALOG_DATA) public data) {

    }

  ngOnInit() {
  }

  closeDialog(): void {
      this.dialogRef.close();
  }

  savePartner() {
   console.log(this.partnerInfomation.value.participantData);
   const url = '';
   this.crossBorderUtilService.save(this.partnerInfomation.value, url).subscribe(
     (response) => {
       this.participantName = this.partnerInfomation.value.participantData.participantName;
       this.dialogRef.updateSize('1000px', '350px');
       this.saveSuccess = true;
     },
     (errors) => {
      console.log(errors);
     }
   );
  }


}
