import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CrossBorderUtilService {

  serviceVariable = 'this is test variable';

  constructor(private http: Http) { }

  save(data: any, url: string): Observable<any> {
    url = 'https://test-http-b3fee.firebaseio.com/data.json';
    const header = new Headers({'content-type' : 'application/json'});
    const options = new RequestOptions({ headers: header });
    return this.http.post( url , data, options);
  }

  get(): Observable<any> {
       return this.http.get('https://test-http-b3fee.firebaseio.com/data.json');
  }

}
