import { CrossBorderUtilService } from './../services/cross-border-util.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort} from '@angular/material';
import { DataTableDataSource } from './data-table-datasource';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-data-table',
  templateUrl: './data-table.component.html',
  styleUrls: ['./data-table.component.css'],
})
export class DataTableComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;


  dataSource: DataTableDataSource;


  /** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
  displayedColumns = ['company', 'status', 'updated', 'details'];

  constructor( private router: Router,
              private route: ActivatedRoute,
              private crossBorderUtilService: CrossBorderUtilService
              ) {
  }

  ngOnInit() {
    this.dataSource = new DataTableDataSource(this.paginator, this.sort , this.crossBorderUtilService);
   // this.getData();
  }


  openDetails() {
    this.router.navigate(['/details'], {relativeTo: this.route});
  }

}
