import { MatDialog } from '@angular/material';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModifyGeneralInformationComponent } from '../modify-general-information/modify-general-information.component';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-partner-details',
  templateUrl: './partner-details.component.html',
  styleUrls: ['./partner-details.component.css']
})
export class PartnerDetailsComponent implements OnInit {
  panelOpenState: Boolean = false;
  toppings = new FormControl();
  toppingList: string[] = ['Provisional ICA', 'Acquiring ICA', 'Billing ICA'];
  constructor(private router: Router, public dialog: MatDialog) {
  }

  ngOnInit() {
  }

  cancel(): void {
    this.router.navigate(['']);
  }

  editSection() {
    this.openDialog();
  }

  openDialog(): void {
    this.dialog.open(ModifyGeneralInformationComponent, {
      height: '550px',
    });
  }


}
